


import { ListBox, ListBoxTwo } from '@/components/ListBox'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import DingTalk from 'DingTalk'

const imagesPath = './static/images/zhengfu/'
export default {
name: 'zhengfu',
components: {
ViewBox,
Swiper,
SwiperItem,
ListBox,
ListBoxTwo
},
data () {
return {
corpId: 'ding5dceb18be2471a5a35c2f4657eb6378f',
_config: {},
imagesPath,
bannerList: [
{ img: imagesPath + 'banner.png' },
{ img: imagesPath + 'banner1.png' }
],
nowTop: 0,
scroll: '',
pageStyle: '',
weatherInfo: {},
messageList: [



],
listBoxData: [








],
listBoxTowData: [




],
listBoxData2: [








],
listBoxTowData2: [




]
}
},
mounted () {
this.$nextTick(() => {
setTimeout(()=>{
this.initScroll()
}, 100)
})
},
created () {

document.title = '标题'
let screemHeight = window.innerHeight
this.pageStyle = 'height:' + screemHeight + 'px'
this.iniDingtalk().then(res => {
alert(JSON.stringify(res))
this.getUserInfo(res)
}).catch(res => {
alert(JSON.stringify(res))
})
this.getWeather()

},
methods: {

























getWeather () {
this.$http.get('https://oa.dingcloud.com.cn/ding-ssm-demo/weather/getweather').then(res => {
this.weatherInfo = res.weatherinfo
})
},

getUserInfo (data) {
this.$http.get(`https://oa.dingcloud.com.cn/ding-ssm-demo/user/code/${data.code}`).then(res => {
alert(JSON.stringify(res))
this.getUsermicrlist(res)
this.getNotice(res)
})
},

getUsermicrlist (data) {
this.$http.get(`https://oa.dingcloud.com.cn/ding-ssm-demo/microapp/getmicrlist/${data.userid}`).then(res => {
alert(JSON.stringify(res))
this.listBoxData = res.dayoffice.map(item => {
item.icon = item.appIcon
item.url = `dingtalk://dingtalkclient/action/switchtab?index=2&name=work&scene=1&corpid=${this.corpId}&agentid=${item.agentId}`
return item
})
this.listBoxData2 = res.integratedservices.map(item => {
item.icon = item.appIcon
item.url = `dingtalk://dingtalkclient/action/switchtab?index=2&name=work&scene=1&corpid=${this.corpId}&agentid=${item.agentId}`
return item
})
this.listBoxTowData = res.daywork.map(item => {
item.icon = item.appIcon
item.url = `dingtalk://dingtalkclient/action/switchtab?index=2&name=work&scene=1&corpid=${this.corpId}&agentid=${item.agentId}`
return item
})
this.listBoxTowData2 = res.consultation.map(item => {
item.icon = item.appIcon
item.url = `dingtalk://dingtalkclient/action/switchtab?index=2&name=work&scene=1&corpid=${this.corpId}&agentid=${item.agentId}`
return item
})
})
},

getNotice (data) {
this.$http.get(`https://oa.dingcloud.com.cn/ding-ssm-demo/blackboard/getBlackboard/${data.userid}`).then(res => {
alert(JSON.stringify(res))
if (res.dingtalk_smartwork_blackboard_listtopten_response) {
this.messageList = res.dingtalk_smartwork_blackboard_listtopten_response.result.blackboard_list.oapi_blackboard_vo
}
})
},

iniDingtalk () {
return new Promise((resolve, reject) => {
this.$http.get('https://oa.dingcloud.com.cn/ding-ssm-demo/ticket/getconfig').then(res => {
this._config = res
this.corpId = this._config.corpId
resolve(res)
})
}).then(res => {
DingTalk.config({
agentId: res.agentid, // 必填，微应用ID
corpId: res.corpId,//必填，企业ID
timeStamp: res.timeStamp, // 必填，生成签名的时间戳
nonceStr: res.nonceStr, // 必填，生成签名的随机串
signature: res.signature, // 必填，签名
type: 1,   //选填。0表示微应用的jsapi,1表示服务窗的jsapi。不填默认为0。该参数从dingtalk.js的0.8.3版本开始支持
jsApiList : [ 'runtime.info', 'biz.contact.choose',
'device.notification.confirm', 'device.notification.alert',
'device.notification.prompt', 'biz.ding.post',
'biz.util.openLink' ] // 必填，需要使用的jsapi列表，注意：不要带dd。
})
return new Promise((resolve, reject) => {

DingTalk.runtime.permission.requestAuthCode({
corpId: res.corpid,
onSuccess (result) {
resolve(result)
console.log(JSON.stringify(result))
alert(JSON.stringify(corpid))
/*{
code: 'hYLK98jkf0m' 
}*/
},
onFail (err) {
reject(err)


}
})

DingTalk.error(function(error){

reject(err)
})
})
})
},

initScroll () {







},












}
}



