export default {
  props: {
    data: {
      type: Array
    },
    iconWidth: {
      type: String,
      default: '3.6rem'
    }
  },
  data () {
    return {
      list: []
    }
  },
  watch: {
    'data': {
      handler (val) {
        this.list = this.splitArray(val, 4)
      },
      deep: true
    }
  },
  methods: {
    splitArray (arr, len) {
      let l = arr.length
      let result = []
      for (let i = 0; i < l; i += len) {
        result.push(arr.slice(i, i + len))
      }
      return result
    },
    open (url) {
      window.location.href = url
    }
  },
  created () {
    this.list = this.splitArray(this.data, 4)
  }
}



