import Vue from 'vue'
import Router from 'vue-router'
import ZhengFu from '@/pages/zhengFu'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/zhengfu'
    },
    {
      path: '/zhengFu',
      name: 'ZhengFu',
      component: ZhengFu
    }
  ]
})



